import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { NgbModal , ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { Location } from "@angular/common";
import { JobAttachmentService } from 'services/attachments/job-attachment.service';
import Swal from 'sweetalert2';
import { LogsService } from 'services/job/logs.service';
declare let swal: any;

@Component({
  selector: 'app-job-attachments',
  templateUrl: './job-attachments.component.html',
  styleUrls: ['./job-attachments.component.scss']
})
export class JobAttachmentsComponent implements OnInit {

  attachmentList=[];
  fileToUpload: File = null;

  closeResult: string;

  constructor(
    private formBuilder: FormBuilder,
    private modalService: NgbModal,
    private toster: ToastrService,
    private _location: Location,
    private attachmentService:JobAttachmentService,
    private logs:LogsService,
  ) { }

  ngOnInit(): void {
    this.getAllAttachments()
  }





  /* -------------------------------------------------------------------------- */
  /*                             Modal Functions start                          */
  /* -------------------------------------------------------------------------- */
  open(content) {
    this.modalService.open(content, { size: 'lg' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }


  // This function is used in open
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }


  openColapse(content){
    console.log(content);
    //  let displayStyle =content.style.display
    if(content.style.display ==='none'){
      content.style.display = 'inline-block'
    } else{
      content.style.display = 'none'
    }
    
  }



  uploadFile(file){
    console.log(file)
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    var date = mm + '-' + dd + '-' + yyyy;
    this.fileToUpload = file.item(0);
    let fileObj={
      title:this.fileToUpload.name,
      size:this.fileToUpload.size,
      date:date
    }
    console.log(fileObj);
    

    this.attachmentService.addAttachment(fileObj).subscribe(data=>{
      this.getAllAttachments()
      console.log(data); 
    })

    let logsObj={
        full_name:"Mohamed Ahmed",
        username:"Mohamed Ahmed",
        type_user:"recruiter",
        description:`Attachment uploaded "${this.fileToUpload.name}"`,
        profile:"",
        created: date,
        type_activity:"Upload Attachment"
    }
    this.logs.addLogs(logsObj).subscribe
  }



  getAllAttachments(){
    this.attachmentService.getAttachment().subscribe(data=>{
      this.attachmentList = data
      console.log(this.attachmentList);      
    })
  }


  deleteAttachment(id){
    console.log(id);
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.value) {
        Swal.fire(
          'Deleted!',
          'Job has been deleted.',
          'success'
        )

        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
        var date = mm + '-' + dd + '-' + yyyy;

        let attachmentfileName = this.attachmentList.find(x=> x.id)
        let logsObj={
          full_name:"Mohamed Ahmed",
          username:"Mohamed Ahmed",
          type_user:"recruiter",
          description:`Attachment deleted "${attachmentfileName.title}"`,
          profile:"",
          created: date,
          type_activity:"Delete Attachment"
      }
      this.logs.addLogs(logsObj).subscribe()
        this.attachmentService.deleteAttachment(id).subscribe(
          (data) => {
            this.getAllAttachments();
          },
          (err) => {
            this.toster.error("We Have Problem Please Try Again", "", {
              timeOut: 3000,
              positionClass: "toast-top-full-width",
              closeButton: true,
            });
          }
        );
      }
    })
    
  }

}
